@media (min-width: 1024px) {
  .form {
    padding-top: 92px;
    padding-bottom: 113px;
    background: url("../img/paper-plane@1x.png") no-repeat 350px 161px,
      url("../img/triangle-with-dots-background.svg") no-repeat 249px 95px,
      url("../img/small-triangle-with-dots-background.svg") no-repeat 197px 292px / 330px 292px,
      $main-blue;

    fieldset {
      border: none;
      width: 570px;
      display: flex;
      flex-wrap: wrap;
      padding: 0;
      margin-left: 10px;
      justify-content: space-between;
      position: relative;
      left: 50%;
    }

    legend {
      margin: 0;
      padding: 0;
      color: #ffffff;
      font-family: $roboto;
      font-size: 36px;
      line-height: 51px;
    }

    span {
      font-family: $PT-Mono;
      font-size: 16px;
      line-height: 22px;
      color: #ffffff;
      margin-top: 10px;
      margin-left: 5px;
    }

    input {
      display: block;
      width: 270px;
      height: 50px;
      box-sizing: border-box;
      background-color: rgba(255, 255, 255, 0.1);
      border: none;
      font-family: $PT-Mono;
      font-size: 16px;
      line-height: 22px;
      color: #ffffff;
      margin-top: 50px;
      padding-left: 17px;
      margin-left: 5px;

      &::placeholder {
        color: #ffffff;
      }

      &:hover,
      &:focus {
        background-color: rgba(255, 255, 255, 0.3);
      }
    }

    textarea {
      display: block;
      width: 570px;
      height: 154px;
      box-sizing: border-box;
      background-color: rgba(255, 255, 255, 0.1);
      border: none;
      font-family: $PT-Mono;
      font-size: 16px;
      line-height: 22px;
      color: #ffffff;
      resize: none;
      margin-top: 40px;
      margin-left: 5px;
      padding-left: 17px;
      padding-top: 12px;

      &::placeholder {
        color: #ffffff;
      }

      &:hover,
      &:focus {
        background-color: rgba(255, 255, 255, 0.3);
      }
    }

    button {
      width: 270px;
      height: 45px;
      border: 1px solid #ffffff;
      background: none;
      font-family: $roboto-bold;
      font-weight: bold;
      font-size: 14px;
      line-height: 16px;
      text-align: center;
      color: #ffffff;
      letter-spacing: 0.05em;
      text-transform: uppercase;
      border-radius: 5px;
      margin-top: 44px;
      margin-left: 5px;
      box-sizing: border-box;
      padding-right: 6px;

      &:hover,
      &:focus {
        transform: scale(1.2);
      }
    }

    label {
      font-family: $PT-Mono;
      font-size: 14px;
      line-height: 16px;
      color: #ffffff;
      opacity: 0.8;
    }

    .form__checkbox {
      width: 290px;

      input {
        width: 16px;
        height: 16px;
        display: none;
      }

      label {
        display: flex;
        position: relative;
        top: 53px;
        left: 59px;

        &::before {
          content: "";
          display: block;
          width: 16px;
          height: 16px;
          border: 1px solid #ffffff;
          box-sizing: border-box;
          position: absolute;
          left: -32px;
        }

        &:hover,
        &:focus {
          &::before {
            transform: scale(1.2);
          }
        }
      }
    }

    .form__checkbox--checked {
      label {
        &::before {
          content: "";
          display: block;
          width: 16px;
          height: 16px;
          border: 1px solid #ffffff;
          box-sizing: border-box;
          position: absolute;
          left: -32px;
          background: url("../img/icon-check.svg") no-repeat center;
        }
      }
    }
  }

  @media (min-width: 1024px) and (max-width: 1280px) {
    .form {
      padding-top: 92px;
      padding-bottom: 113px;
      background: url("../img/paper-plane@1x.png") no-repeat 100px 161px / 25% 25%,
        url("../img/triangle-with-dots-background.svg") no-repeat 100px 95px,
        url("../img/small-triangle-with-dots-background.svg") no-repeat 60px 292px / 330px 292px,
        $main-blue;


      fieldset {
        width: 470px;
        left: 35%;
      }
    }
  }

  @media (min-width: 1281px) and (max-width: 1440px) {
    .form {
      padding-top: 92px;
      padding-bottom: 113px;
      background: url("../img/paper-plane@1x.png") no-repeat 150px 161px / 30% 30%,
        url("../img/triangle-with-dots-background.svg") no-repeat 100px 95px,
        url("../img/small-triangle-with-dots-background.svg") no-repeat 60px 292px / 330px 292px,
        $main-blue;
    }
  }

  @media (min-width: 1441px) and (max-width: 1701px) {
    .form {
      padding-top: 92px;
      padding-bottom: 113px;
      background: url("../img/paper-plane@1x.png") no-repeat 250px 161px / 30% 30%,
        url("../img/triangle-with-dots-background.svg") no-repeat 149px 95px,
        url("../img/small-triangle-with-dots-background.svg") no-repeat 97px 292px / 330px 292px,
        $main-blue;
    }
  }
}

